

.contact{
    background-color: #796a53;
    padding: 6rem;
    color: white;
}

#contactBox{
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}



@media all and (min-width: 250px) and (max-width: 425px)
{
    .hrClass{
        width: 100vh
    }

    .contact{
        width:100%;
        height: auto;
    }

    #contactFlex{
        width: 100%;
    }

    #contactBox{
        width: 80%;
        margin: auto;
    }
}
