.slick-prev:before, .slick-next:before {
    font-size: 30px;
    color: grey;
}
.slick-slide > div {
    width: 75%;
    margin: auto;
    margin: 0 40px;
}
.slick-list {
    margin: 0 -10px;
}

#stats{
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.projectFlex{
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    background-color: #3B3228;
    border-radius: 15px;
    opacity: 1;
    box-shadow:rgba(0, 0, 0, 0.35) 0px 5px 15px ;
    padding: 20px;
}

.projects{
    background-color: #796a53;
    padding-top: 6rem;
}

#statsFlex{
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.statsName{
    font-size: 40px;
    font-weight: bold;
    color: #15110D;
}

#calenderImg{
    display:block;
    margin:auto;
    /* width:90%; */
    height:20vh
}


@media all and (min-width: 250px) and (max-width: 425px)
{

    .hrClass{
        width: 100vh
    }

    .projects{
        width: 80%;
    }

    .projectFlex{
        padding: 10px;
        margin: auto;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .imageBox{
        width: 100%;
        margin: auto;
    }

    .statsName{
        font-size: 15px;
    }

    #statsFlex{
        flex-direction: column;
    }

    #calenderImg{
        height:0vh
    }

}

@media all and (min-width: 426px) and (max-width: 670px)
{
    .hrClass{
        width: 100vh
    }

    .statsName{
        font-size: 20px;
    }

    .projects{
        width: 100%;
        margin: auto;
    }

    .projectFlex{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: auto;
        padding: 2px;
    }

    .imageBox{
        /* height: 70vh; */
        width: 70%;
        margin: auto;
    }

    #calenderImg{
        height: 10vh;
        padding: 10px;
    }

    .slider{
        width: 100%;
        margin: auto;
    }

}


@media all and (min-width: 671px) and (max-width: 1200px)
{
    /* .projects{
        width: 100vh;
    } */

    .projectFlex{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: auto;
    }

    .imageBox{
        width: 80%;
        margin: auto;
        border-radius: 10px;
        height: 70vh;
    }

    .slider{
        width: 125%;
        margin: auto;
    }

    .statsName{
        font-size: 25px;
    }

    #calenderImg{
        /* width: 100%; */
        height: 10vh;
        padding: 0;
    }
}
  
  