.skillGrid{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    justify-content: center;
    width: 80%;
    margin: auto;
    gap: 10px;
}

/* #skillFlex{
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: center;
    gap: 10px;
} */

.skillSet{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    background-color: #d6d0c9;
    color: #15110D;
    border: 10px solid #3b3228;
    box-shadow: 0px 0px 15px  #3b3228;
}

.skillImg{
    width: 50%;
    padding: 2px
}

@media all and (min-width: 250px) and (max-width: 425px)
{
    /* .hrClass{
        width: 100%
    } */

    /* .skills{
        width: 100%;
        height: auto;
    } */

    #skillFlex{
        width: 100%;
    }

    .skillGrid{
        grid-template-columns: repeat(2,1fr);
    }

}

@media all and (min-width: 426px) and (max-width: 670px)
{
    /* .hrClass{
        width: 100%
    } */

    .skills{
        width: 100%;
        height: auto;
    }

    .skillGrid{
        grid-template-columns: repeat(3,1fr);
        gap: 25px;
    }



}

@media all and (min-width: 671px) and (max-width: 1200px)
{
   
    .skills{
        width: 100%;
        height: auto;
    }

    #skillGrid{
        grid-template-columns: repeat(5,1fr);
    }
}
  
  