
   .resumeBtn {
    margin-top: 1rem;
    align-items: center;
    border-radius: 4em;
    font-weight: bold;
    color: black;
    background: 'orange';
    box-shadow: 0px 5px 18px rgba(136, 35, 35, 0.6);
  }

  #aboutme{
    background-color: #796a53;
    padding-top: 6rem;
  }

  .aboutmeClass{
    display: flex;
    flex-direction: row;
    width: 80%;
    margin: auto;
    justify-content:space-between;
    gap: 100px;
    align-items: center;
  }

  

  #aboutImgBox{
    width: 100%;
    flex: 3;
  }

  .homeimg{
    width: 80%;
    margin: auto;
    border-radius: 25px;
    border: 2px dashed teal;
  }

  .textAboutme{
    text-align: justify;
    font-size: 22px;
  }

  .textAboutmehead{
    font-size: 20px;
  }

  .textAboutmespan{
    font-size: 20px;
  }
  
  @media all and (min-width: 250px) and (max-width: 425px)
  {

    .hrClass{
      width: 100vh
    }

    .aboutme{
      height: 70vh;
    }

    .aboutmeClass{
      flex-direction: column;
      gap: 10px;
    }

    .homeimg{
      width: 60%;
      margin: auto;
    }

    .textAboutme{
      /* text-align:center; */
      font-size: 25px;
    }

    .textAboutmehead{
      font-size: 30px;
    }

    .textAboutmespan{
      font-size: 25px;
    }

    .resumeBtn {
      width: 40%;
      font-weight: bold;
      font-size: 25px;
    }
  }

   
  @media all and (min-width: 426px) and (max-width: 670px)
  {
    .hrClass{
      width: 100vh
    }

    .aboutme{
      height: auto;
    }

    .aboutmeClass{
      flex-direction:column;
    }

    .homeimg{
      width: 60%;
      margin: auto;
    }

    .textAboutme{
      /* text-align:center; */
      font-size: 25px;
    }

    .textAboutmehead{
      font-size: 30px;
    }

    .textAboutmespan{
      font-size: 25px;
    }
  }

 
  @media all and (min-width: 671px) and (max-width: 1200px)
  {
    .aboutmeClass{
      flex-direction: column;
      /* justify-content: 'space-evenly'; */
      gap: 10px;
    }

    .homeimg{
      width: 60%;
      margin: auto;
    }

    .textAboutme{
      /* text-align:center; */
      font-size: 25px;
    }

    .textAboutmehead{
      font-size: 30px;
    }

    .textAboutmespan{
      font-size: 25px;
    }


  }

  
  